import React, {Component} from 'react';
import PropTypes from "prop-types";

export const RenderIf = (props) => {

    //   props.condition
    if(props.condition){

        //  ,   
        return props.children;
    } else {

        //   
        return null;
    }
}

RenderIf.propTypes = {
    condition: PropTypes.bool,
    children: PropTypes.any
}

export default RenderIf;


